import React, {useState, useEffect} from 'react';
import { Card } from 'react-bootstrap';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add'

import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import TextField from '@material-ui/core/TextField';

export default function Photo({image}) {
  const [open, setOpen] = useState(false);

  const ShowInfoModal = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  // состояние для выбраного файла
  const [selectedFile, setSelectedFile] = useState()
  // состояние для создания превью картинки
  const [preview, setPreview] = useState()
  // проверка на отсуствие файла для его подальшего добавления
  useEffect(() => {
      if (!selectedFile) {
          setPreview(undefined)
          return
      }
    
      const objectUrl = URL.createObjectURL(selectedFile)
      setPreview(objectUrl)
      
      console.log(objectUrl);
      // добавление новой фотографии
      const selector = document.querySelector('.MuiGrid-root')
      selector.insertAdjacentHTML('beforeend', `<div class="MuiGrid-root MuiGrid-item MuiGrid-grid-xs-12 MuiGrid-grid-sm-6 MuiGrid-grid-md-3 MuiGrid-grid-lg-4">
        <div class="MuiPaper-root makeStyles-paper-2 MuiPaper-elevation1 MuiPaper-rounded">
        <button class="MuiButtonBase-root MuiButton-root MuiButton-text" tabindex="0" type="button"><span class="MuiButton-label">
          <div class="cardAdd">
            <img class="card-img card card-img" src="${objectUrl}">
          </div>
        </div>` )
      return () => URL.revokeObjectURL(objectUrl)
  }, [selectedFile])

  const onSelectFile = e => {
    
    setSelectedFile(e.target.files[0])
    // конвертация объекта картинки в массив, 
    // для последующих манипуляций
    const files = Array.from(e.target.files)
    files.forEach(file => {
      const selector = document.querySelector('.MuiGrid-root')
      selector.insertAdjacentHTML('beforeend', `"<h1 class='fileName'>${file.name}</h1>` )
    })
  }

  if (image.photo != null) {
    return (
      <Button>
        <div 
          className='card'
          onClick={Photo}
        >
        <Card.Img 
        className='card' 
        src={image.photo} 
        onClick={ShowInfoModal}

        />
        <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div>
         <h2 className='modal_header'> Saved photo</h2>
          <div>
          <Card.Img className='card' src={image.photo} />
          </div>
      <Button
        className='modalBtn'
        variant="contained"
        color="default"
        style={{background: '#ff9800', color: 'white', marginTop: '20px'}}
      >
        Add account
      </Button>
          </div>
        </Fade>
      </Modal>
      </div>
      </Button>
    );
  }

  function Photo(objectUrl) {
    console.log(objectUrl.target.src);
  }
  
    return (
      <Button type='file'>
      <div className='cardAdd'>
          {selectedFile &&  <Card.Img
           className='card card-img' 
           src={preview} 
           onClick={ShowInfoModal}
           /> }
          <Fab color="primary" >
          <input 
           className='input'
           type="file" 
           onChange={onSelectFile}
           /> 
            <AddIcon />
          </Fab> 
      </div>
      </Button>
    )
 
}